@media only screen and (min-width: 800px) {
    #window {
        height: 100%;
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
    }

    #window-container {
        display: flex;
        flex-direction: row-reverse;
        margin: 50px;
        background-color: black;
        width: 90%;
        height: 90%;
        border-radius: 20px;
        overflow-y: scroll;
    }
}

@media only screen and (max-width: 800px) {
    #window {
        border-left: black 1px solid;
        border-top: black 1px solid;
        border-right: black 1px solid;
        padding: 20px;
    }

    #window-container {
        box-shadow: 5px 5px;
        height: 100%;    
        width: 100%;
        overflow-y: scroll;
        background: black;
    }

    #playing-and-navigation-container {
        border-left: black 1px solid;
        position: fixed;
        left: 0;
        bottom: 0;
        width: 100%;
    }
  }